import React from "react";
import { motion } from "framer-motion";
import {
  AboutContainer,
  Container,
  SkillsContainer,
  SkillsSection,
  TextBg
} from "./AboutStyle";
import { IoLogoJavascript as JS } from "react-icons/io";
import {
  FaGitAlt as GIT,
  FaBootstrap as Bootstrap,
  FaReact as REACT,
  FaSass as SASS,
  FaCss3Alt as CSS,
  FaHtml5 as HTML
} from "react-icons/fa";
import { DiNodejsSmall as NodeJs, DiMongodb as MongoDB } from "react-icons/di";
import { SiRedux as Redux } from "react-icons/si";

const Animation = {
  h1: {
    x: "-40vw",
    transition: {
      yoyo: Infinity,
      duration: 25,
      ease: "linear"
    }
  },
  h2: {
    x: "50vw",
    transition: {
      yoyo: Infinity,
      duration: 40,
      ease: "linear"
    }
  }
};
function About() {
  return (
    <SkillsSection id="skills">
      <h1 className="title">About / Skills</h1>
      <TextBg>
        <motion.h1 variants={Animation} animate="h1">
          About
        </motion.h1>
        <motion.h2 variants={Animation} animate="h2">
          Skills
        </motion.h2>
      </TextBg>
      <Container>
        <AboutContainer>
          <p>
            {" "}
            Hi, My name is Myrat Charyyev and I'm passionate Full Stack
            Developer using front-end and back-end technologies to build amazing
            products and focusing on solving problems for different niches and
            different industries using the power of technology.{" "}
          </p>
          <p>
            I will love to hear from you. Whether it's a project, job
            opportunity, or just a chat. Feel free to contact me.{" "}
          </p>
        </AboutContainer>
        <SkillsContainer>
          <ul>
            <li>
              <HTML />
            </li>
            <li>
              <CSS />
            </li>
            <li>
              <JS />
            </li>
            <li>
              <SASS />
            </li>
            <li>
              <GIT />
            </li>
            <li>
              <REACT />
            </li>
            <li>
              <Redux />
            </li>
            <li>
              <NodeJs />
            </li>
            <li>
              <MongoDB />
            </li>
            <li>
              <Bootstrap />
            </li>
          </ul>
        </SkillsContainer>
      </Container>
    </SkillsSection>
  );
}

export default About;
