import { motion } from "framer-motion";
import styled from "styled-components";

export const ProjectsSection = styled.section`
  background: ${({ theme }) => theme.body};

  max-height: 1000vh;
  min-width: 100vw;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-auto-flow: column;
  gap: 25px;
  row-gap: 25px;
  padding: 70px 60px;
  position: relative;
  // z-index: 4;
`;

export const Title = styled.h1`
  position: absolute;
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  top: 0;
  left: 60px;
  color: ${({ theme }) => theme.text};
  pointer-events: none;
`;

export const Container = styled(motion.div)`
  width: min(700px, 90vw);
  height: min(300px, 90vh);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
  background: ${({ theme }) => theme.projectBg};
  color: ${({ theme }) => theme.projectText};
  position: relative;
  display: flex;
  border-radius: 10px;
  overflow: hidden;
  transition: background 0.3s;
  // transition: color 0.3s;

  &::after {
    position: absolute;
    content: "Tap for more information";
    inset: 0;
    background-color: #000;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 20px;
    transition: all 0.5s;
    opacity: 0;
  }

  .test {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
  }

  &:hover::after {
    opacity: 1;
    cursor: pointer;
  }
`;

export const LeftSection = styled(motion.div)`
  min-width: min(240px, 35vw);
  max-width: min(240px, 35vw);
  height: 100%;
  // border-right: 1px solid black;
  border-radius: 10px 0 0 10px;
  position: relative;
  // padding: 10px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: ;

  h1 {
    width: 50%;
    text-align: center;
    width: 100%;
    // white-space: nowrap;
    font-size: 25px;
    margin: 10px 0;
  }

  li {
    font-size: 1.1rem;
    margin: 10px 20px;
    // padding: 10px 20px;
  }
  p {
    font-size: 15px;
    font-style: italic;
    margin: 10px;
    opacity: 0.7;
  }

  div {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;

    @media screen and (max-width: 400px) {
      display: block;
      flex-direction: column;
    }
  }
  .button {
    width: min(90px, 16vw);
    font-size: clamp(0.5em, 3vw, 1em);
    // font-size:clamp(40px, 4vw, 5em);
    color: #fff;
    text-decoration: none;
    background: #162936;
    white-space: nowrap;
    outline: none;
    border: none;
    cursor: pointer;
    text-decoration: none;
    transition: 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    padding: 8px 14px;
    border-radius: 10px;
    width: 90px;
    height: min-content;

    @media screen and (max-width: 400px) {
      width: 100%;

      &:nth-child(1) {
        margin-bottom: 10px;
      }
    }
  }
`;

export const RightSection = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
`;
