import styled from "styled-components";
import { motion } from "framer-motion";

export const SkillsSection = styled.section`
  max-height: 100vh;
  width: calc(100vw + 10px);
  padding: 70px 70px;
  position: relative;
  background: ${({ theme }) => theme.body};
  // display: grid;
  // grid-template-columns: repeat(3, 1fr);
  // grid-template-rows: repeat(2, 1fr);
  // gap: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;

  .title {
    position: absolute;
    font-family: "Montserrat", sans-serif;
    font-size: 50px;
    top: 0;
    left: 100px;
    color: ${({ theme }) => theme.text};
    z-index: 2;
    pointer-events: none;
    transition: color 0.3 ease;
  }
  @media screen and (max-width: 1000px) {
    width: 200vw;
  }
  @media screen and (max-width: 500px) {
    width: 300vw;
  }
`;

export const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  z-index: 2;

  @media screen and (max-width: 800px) {
  }
`;

export const AboutContainer = styled.div`
  // height: min(350px, 90vh);
  min-height: 350px;
  width: min(42vw, 90vw);
  background: ${({ theme }) => theme.projectBg};
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
  padding: 40px;
  color: ${({ theme }) => theme.projectText};
  transition: background 0.3 ease;
  transition: color 0.3 ease;

  p {
    font-size: clamp(20px, 3vw, 1em);
    font-weight: 500;
    line-height: 1.5;
    padding-top: 15px;
    pointer-events: none;
  }

  @media screen and (max-width: 800px) {
    width: 600px;
    height: 350px;
  }
  @media screen and (max-width: 500px) {
    min-width: 600px;
    height: 350px;
  }
`;
export const SkillsContainer = styled.div`
  // height: min(350px, 90vh);
  min-height: 350px;
  width: min(42vw, 90vw);
  background: ${({ theme }) => theme.projectBg};
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
  padding: 40px;

  ul {
    display: flex;
    align-items: center;
    justify-content: center;

    flex-wrap: wrap;

    li {
      list-style: none;
      padding: 15px;
      margin: 10px;
      background: gray;
      // font-size: 60px;
      font-size: clamp(60px, 3vw, 1em);
      border-radius: 5px;
      text-transform: uppercase;
      // color: #162936;
    }
  }

  @media screen and (max-width: 800px) {
    width: 600px;
    height: 350px;
  }
  @media screen and (max-width: 500px) {
    min-width: 600px;
    height: 350px;
  }
`;

export const TextBg = styled(motion.h1)`
  position: absolute;
  top: -150px;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: clamp(200px, 29vw, 40em);
  letter-spacing: 5px;
  -webkit-text-fill-color: #1b4f72;
  -webkit-text-stroke: 1px black;
  line-height: 1.1;
  opacity: 0.15;
  z-index: 1;
  user-select: none;

  h1 {
    position: absolute;
    left: -50px;
    font-size: clamp(200px, 29vw, 40em);
  }
  h2 {
    position: absolute;
    bottom: -100px;
    font-size: clamp(150px, 29vw, 40em);
  }
`;
