import styled, { css } from "styled-components";
import { FaHome } from "react-icons/fa";
import { AiFillProject } from "react-icons/ai";
import { AiFillGithub } from "react-icons/ai";
import { AiFillLinkedin } from "react-icons/ai";
import { GoTools } from "react-icons/go";
import { IoIosGlobe } from "react-icons/io";

export const NavbarContainer = styled.nav`
  position: fixed;
  bottom: 7px;
  left: 50%;
  transform: translate(-50%, 0);
  // background: ${({ theme }) => theme.navbar};
  // box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.37);
  // backdrop-filter: blur(5px);
  // backdrop-filter: blur(5px);
  // -webkit-backdrop-filter: blur(5px);
  border-radius: 10px;
  // border: 1px solid ${({ theme }) => theme.navbarborder};
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  // overflow: hidden;

  box-shadow: 0px 5px 50px rgba(0, 0, 0, 0.45);

  // transition: all 0.3s;

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    background: ${({ theme }) => theme.navbar};
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid ${({ theme }) => theme.navbarborder};
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    transition: all 0.3s;
    z-index: -2;
  }

  &:hover::before {
    background-color:#000;
    opacity:0.5;
    }


  hr {
    display: block;
    width: 2px;
    height: 40px;
    background: black;
    margin: 0 5px;
    transform: translateY(-2px);
  }
}
`;

const NavIcon = css`
  font-size: 37px;
  color: ${({ theme }) => theme.iconColor};
  margin: 10px 5px;
  cursor: pointer;
  z-index: 3;
  transition: transform 0.3s ease;

  &:hover {
    transform: translateY(-5px);
  }
`;

export const Github = styled(AiFillGithub)`
  ${NavIcon}// border-left: 1px solid black;
`;
export const Linkedin = styled(AiFillLinkedin)`
  ${NavIcon};
`;

export const HomeIcon = styled(FaHome)`
  ${NavIcon};
`;

export const ProjectIcon = styled(AiFillProject)`
  ${NavIcon};
  // transform: rotate(-90deg);
`;
export const SkillsLogo = styled(GoTools)`
  ${NavIcon};
  font-size: 30px;
`;
export const Globe = styled(IoIosGlobe)`
  ${NavIcon};
  font-size: 42px;
  transform: translateY(-2px);
`;
