import React, { useState, useEffect } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import HorizontalScroll from "react-scroll-horizontal";
import { ThemeProvider } from "styled-components";
import { AppContainer } from "./AppStyle";
import About from "./components/About/About";
import Header from "./components/Header/Header";
import Navbar from "./components/Navbar/Navbar";
import Projects from "./components/Projects/Projects";
import { darkTheme, GlobalStyle, lightTheme } from "./globalStyle";

import { useTranslation } from "react-i18next";

const App = () => {
  const [theme, setTheme] = useState("dark");

  //   darkTheme
  //save to localStorage
  const setMode = (mode) => {
    window.localStorage.setItem("theme", mode);
    setTheme(mode);
  };

  const toggleTheme = () => {
    theme === "dark" ? setMode("light") : setMode("dark");
  };

  useEffect(() => {
    const localTheme = window.localStorage.getItem("theme");
    localTheme ? setTheme(localTheme) : setMode("dark");
  }, []);

  const themeMode = theme === "light" ? lightTheme : darkTheme;

  // i18next

  const { t } = useTranslation();

  // function handleClick(lang) {
  //   i18n.changeLanguage(lang);
  // }

  return (
    <ThemeProvider theme={themeMode}>
      {/* <nav style={{ width: "100%", padding: "2rem", background: "cyan" }}>
        <button onClick={() => handleClick("en")}>English</button>
        <button onClick={() => handleClick("ru")}>Russian</button>
        <button onClick={() => handleClick("cn")}>Chinese</button>
        <p>{t("home_title")}</p>
      </nav> */}
      {/* <p>{t("home_header_name")}</p> */}
      <GlobalStyle />
      <AppContainer>
        <BrowserRouter>
          <Navbar theme={theme} toggleTheme={toggleTheme} />
          <HorizontalScroll reverseScroll={true}>
            <Header />
            <Projects />
            <About />
          </HorizontalScroll>
        </BrowserRouter>
      </AppContainer>
    </ThemeProvider>
  );
};

export default App;
