import styled from "styled-components";
import { MdKeyboardArrowRight, MdArrowForward } from "react-icons/md";
import { motion } from "framer-motion";

export const HeaderContainer = styled.section`
  background: ${({ theme }) => theme.body};
  // background: red;
  max-height: 100vh;
  width: 100vw;

  // max-height: 100vh;
  // padding:100px;
  // padding:clamp(1em, 11.5vw, 80px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${({ theme }) => theme.text};
  position: relative;
  overflow: hidden;
`;

export const TextBg = styled(motion.h1)`
  position: absolute;
  top: -150px;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: clamp(300px, 29vw, 40em);
  letter-spacing: 5px;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px ${({ theme }) => theme.text};
  line-height: 1.1;
  opacity: 0.15;
  // z-index: 10;
  user-select: none;

  span {
    position: absolute;
    left: -50px;
    // font-size: 300px;
  }
  .charyyev {
    position: absolute;
    bottom: -100px;
  }
`;
export const HeaderContent = styled.div`
  width: min(1400px, 80%);
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 4;

  h1 {
    font-size: clamp(40px, 8vw, 5em);
    letter-spacing: 4px;
    margin-bottom: 10px;
    @media screen and (max-width: 600px) {
      letter-spacing: 2px;
    }
  }
`;
export const HeaderText = styled.div`
  display: flex;
  align-items: center;
  z-index: 4;
  pointer-events: none;

  h2 {
    margin-right: 50px;
    //font-size:clamp(40px, 8vw, 5em);
    font-size: clamp(30px, 5vw, 3em);
    z-index: 5;
    font-weight: 400;
    display: block;
    // white-space:nowrap;

    // span {
    //   color:#fff;
    // }

    @media screen and (max-width: 600px) {
      display: grid;
      // flex-direction:column;
      grid-template-rows: 1fr;
    }
  }
`;

export const ArrowForwards = styled(MdArrowForward)`
  margin-left: 20px;
`;

export const ArrowRight = styled(MdKeyboardArrowRight)`
  margin-left: 20px;
`;
