import React, { useState } from "react";
import { ProjectsData } from "./ProjectsData";
import {
  Container,
  LeftSection,
  ProjectsSection,
  RightSection,
  Title
} from "./ProjectsStyle";
import { AnimatePresence, AnimateSharedLayout } from "framer-motion";
import Project from "./Project/Project";

const Projects = () => {
  //ModalOpen
  const [modalOpen, setModalOpen] = useState(false);

  // test
  const [layoutId, setlayoutId] = useState(false);

  const expander = (id) => {
    if (modalOpen !== true) {
      setlayoutId(id);
      setModalOpen(true);
    } else {
      setlayoutId(null);
      setModalOpen(false);
    }
  };

  return (
    <AnimateSharedLayout type="crossfade">
      <ProjectsSection id="projects">
        <Title className="title">Projects</Title>
        {ProjectsData.map((item) => {
          return (
            <Container
              layoutId={item.id}
              onClick={() => expander(item.id)}
              key={item.id}
              className="test"
            >
              <LeftSection>
                <h1>{item.header}</h1>
                <ul>
                  <li>{item.content}</li>
                  <li>{item.contentTwo}</li>
                  {item.contentThree ? <li>{item.contentThree}</li> : ""}
                  {item.contentFour ? <li>{item.contentFour}</li> : ""}
                  {item.contentFive ? <li>{item.contentFive}</li> : ""}
                </ul>
                <div className="comment">
                  <p>
                    {item.comment.length <= 40
                      ? item.comment
                      : `${item.comment.slice(0, 40)}...`}
                  </p>
                </div>
              </LeftSection>
              <RightSection>
                <img
                  src={process.env.PUBLIC_URL + `/images/${item.id}/1.jpg`}
                />
              </RightSection>
            </Container>
          );
        })}
        <AnimatePresence>
          {modalOpen && <Project expander={expander} id={layoutId} />}
        </AnimatePresence>
      </ProjectsSection>
    </AnimateSharedLayout>
  );
};

export default Projects;
