import styled from "styled-components";
import { motion } from "framer-motion";

export const Backdrop = styled(motion.div)`
  position: absolute;
  // top: 0;
  // bottom: 0;
  height: 100vh;
  width: 100%;
  background: #000000e1;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
`;

export const Title = styled(motion.h1)`
  text-align: center;
  width: 100%;
  font-size: 40px;
`;

export const ProjectContainer = styled(motion.div)`
  position: relative;
  width: 80vw;
  height: 80vh;
  background: ${({ theme }) => theme.projectBg};
  color: ${({ theme }) => theme.projectText};
  border-radius: 20px;
  display: flex;
  overflow: hidden;
  // margin: auto;

  // position: absolute;
  // position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);

  .test {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
  }

  .back-btn {
    position: absolute;
    top: -30px;
    right: -30px;
    font-size: 3rem;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    background: #fff;
  }
`;

export const LeftSection = styled(motion.div)`
  min-width: min(440px, 25vw);
  max-width: min(440px, 25vw);
  // max-width: 25vw;
  height: 100%;
  // border-right: 1px solid black;
  // border-radius: 10px 0 0 10px;
  position: relative;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: ;

  li {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  p {
    // position: absolute;
    font-size: 25px;
    font-style: italic;
    bottom: 80px;
    opacity: 0.7;
  }

  div {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;

    @media screen and (max-width: 400px) {
      display: block;
      flex-direction: column;
    }
  }
  .button {
    width: min(150px, 16vw);
    font-size: clamp(1em, 3vw, 2em);
    // font-size:clamp(40px, 4vw, 5em);
    color: #fff;
    text-decoration: none;
    background: #162936;
    white-space: nowrap;
    outline: none;
    border: none;
    cursor: pointer;
    text-decoration: none;
    transition: 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    padding: 8px 14px;
    border-radius: 10px;

    @media screen and (max-width: 400px) {
      width: 100%;

      &:nth-child(1) {
        margin-bottom: 10px;
      }
    }
  }

  .languages {
    display: flex;
    flex-direction: column;

    .progressBar {
      display: flex;
      width: 100%;
      height: 10px;
      border-radius: 10px;
      overflow: hidden;

      span {
        margin: 0 1px;
      }
    }
  }

  .tools {
    display: grid;
    grid-template-columns: 1fr 1fr;
    white-space: wrap;
  }

  .percentage {
    margin: 0 10px;
    font-weight: 200;
  }

  .tool {
    display: flex;
    align-items: center;
    margin-right: 30px;
    height: min-content;

    span {
      width: 7px;
      height: 7px;
      border-radius: 50%;
      margin-right: 5px;
    }
  }
`;

export const RightSection = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  // border-radius: 0px 10px 10px 0px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
`;
